.main-section-cnt {
  z-index: 100;
  width: 100%;
  height: 100vh;
  display: flex;
  /* background-color: rebeccapurple; */
  background-image: url(/IntelliHack-2K21/static/media/bg2.87aab9cc.jpg);
  background-color: black;

  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.main-section-content {
  margin-left: 3vw;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  /* background-color: red; */
  position: relative;
}

.clock-cnt {
  margin-top: 100px;
  /* background-color: #62ea34; */
  display: flex;
  flex-direction: column;

  -webkit-animation: fadeinClockDiv 5s;
  animation: fadeinClockDiv 5s;
}

.clock-text {
  color: white;
  font-family: "Poppins", sans-serif;
  font-size: 30px;
}

@keyframes fadeinClockDiv {
  from {
    margin-left: 200px;
    opacity: 0;
  }
  to {
    opacity: 1;
    margin-left: 0px;
  }
}

@-webkit-keyframes fadeinClockDiv {
  from {
    opacity: 0;
    margin-left: 200px;
  }
  to {
    opacity: 1;
    margin-left: 0px;
  }
}

.main-section-main-text {
  font-size: 90px;
  color: white;
  /* font-family: "Poppins", sans-serif; */
  -webkit-animation: fadeinMainText 2s;
  animation: fadeinMainText 2s;
}

.white-logo {
  height: 200px;
  /* position: absolute; */
  top: 20px;
  -webkit-animation: fadeinMainText 4s;
  animation: fadeinMainText 4s;
}

@keyframes fadeinMainText {
  from {
    margin-left: 200px;
    opacity: 0;
  }
  to {
    opacity: 1;
    margin-left: 0px;
  }
}

@-webkit-keyframes fadeinMainText {
  from {
    margin-left: 200px;
    opacity: 0;
  }
  to {
    opacity: 1;
    margin-left: 0px;
  }
}

.main-section-sub-text {
  color: white;
  font-size: 28px;
  margin-top: 20px;
  color: #62ea34;
  -webkit-animation: fadeinSubText 3s;
  animation: fadeinSubText 3s;
}

@keyframes fadeinSubText {
  from {
    margin-left: 200px;
    opacity: 0;
  }
  to {
    opacity: 1;
    margin-left: 0px;
  }
}

@-webkit-keyframes fadeinSubText {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

