.navbar-cnt {
  display: flex;
  width: 100vw;
  height: 60px;
  background-color: white;
  position: fixed !important ;
  top: -60px;
  align-items: center;
  transition: all 0.4s ease 0.15s;
  z-index: 1000;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  justify-content: space-between;
}

.navbar-display {
  top: 0px;
}

.navbar-img-cnt {
  margin-left: 200px;
  cursor: pointer;
  transition: all 0.5s ease 0.15s;
}

.navbar-img {
  height: 40px;
}

.navbar-items {
  display: flex;
  color: black;
  margin-right: 200px;
  height: 100%;
  align-items: center;
  justify-content: space-between;
  width: 50%;
  font-size: 16px;
  transition: all 0.5s ease 0.15s;
  /* font-family: "Montserrat", sans-serif; */
}

.navbar-item {
  display: flex;
  align-items: center;
  margin-top: 4px;
  cursor: pointer;

  width: -webkit-fit-content;

  width: -moz-fit-content;

  width: fit-content;
}

.navbar-item-cnt {
  position: relative;
  height: 40px;
  display: flex;
  align-items: center;
  cursor: pointer;

  transition: all 0.2s ease 0.15s;
}

.navbar-item-cnt:hover .navbar-not-clicked {
  width: 100%;
}

.mobile-menu-item-cnt {
  display: none;
}

.navbar-clicked {
  position: absolute;
  border-radius: 10px;
  bottom: 0;
  width: 0;
  height: 4px;
  background-color: #254495;
  transition: all 0.3s ease 0.15s;
}

.navbar-not-clicked {
  position: absolute;
  border-radius: 10px;
  bottom: 0;
  width: 0px;
  height: 4px;
  background-color: #254495;
  transition: all 0.3s ease 0.15s;
}

.navbar-menu {
  display: none;
  width: 30px;
  height: 30px;
  /* border: 1px solid black; */
  margin-right: 20px;
  /* background-color: greenyellow; */
  cursor: pointer;
}

.mobile-menu-item-cnt {
  display: none;
}

.mobile-menu-item-cnt-display {
  display: none;
}

@media (max-width: 1430px) {
  .navbar-items {
    display: flex;
    margin-right: 50px;
    color: black;

    height: 100%;
    align-items: center;
    justify-content: space-between;
    width: 800px;
    font-size: 16px;
    /* font-family: "Montserrat", sans-serif; */
  }
}

@media (max-width: 1430px) {
