  }
}

@media (max-width: 1100px) {
  .navbar-menu {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .navbar-items {
    display: none;
  }

  .navbar-img-cnt {
    margin-left: 10px;
  }

  .mobile-menu-row {
    display: flex;
    /* background-color: rebeccapurple; */
    justify-content: space-between;
    align-items: center;

    width: 100%;
  }

  .navbar-display {
    flex-direction: column;
    justify-content: center;
    /* height: 300px; */
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }

  .navbar-menu {
    margin-top: 15px;
    margin-bottom: 15px;
  }

  .mobile-menu-item-cnt-display {
    display: block;
    margin-bottom: 20px;
