  }

  .mobile-row {
    /* background-color: red; */
    width: 100%;
    margin-left: 10px;
    padding: 10px;
    font-size: 16px;
    cursor: pointer;
  }

  .mobile-row:hover {
    background-color: #254495;
    color: white;
  }
}

